export const validation = event => {
    const {
        IPN,
        city,
        country,
        fatherName,
        firstName,
        issuedBy,
        lastName,
        secretWord,
        passportNum,
        uniqueNumber,
        validUntil,
        birthday,
        issuedDate,
        email,
        phone,
        sex,
        doc
    } = event

    const errors = {}

    const dateValidator = value => {
        const patternTest = /([0]?[1-9]|[1|2][0-9]|[3][0|1])[.]([0]?[1-9]|[1][0-2])[.]([0-9]{4})$/;
        if (!patternTest.test(value)) {
            return '    ..'
        }
    }

    if (!firstName) errors.firstName = '    '
    if (!lastName) errors.lastName = '    '

    if (!birthday) {
        errors.birthday = '    '
    } else {
        errors.birthday = dateValidator(event.birthday)
    }

    if (validUntil) errors.validUntil = dateValidator(event.validUntil)

    if (!issuedDate) {
        errors.issuedDate = '    '
    } else {
        errors.issuedDate = dateValidator(event.issuedDate)
    }

    if (!country) errors.country = '    '
    if (!city) errors.city = '    '

    if (!secretWord) {
        errors.secretWord = '    '
    } else {
        if (secretWord.length < 6) errors.secretWord = '     6 '
    }

    if (!issuedBy) errors.issuedBy = '    '
    if (sex === '----') errors.sex = ' '
    if (doc === '----') errors.doc = '  ,   '

    if (email) {
        const patternEmail = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
        if (!patternEmail.test(email)) {
            errors.email = '   '
        }
    }

    if (phone) {
        const patternPhone = /^[\+]{0,1}380([0-9]{9})$/;
        if (!patternPhone.test(phone)) {
            errors.phone = '  . : +380939998877';
        }
    }

    if (!IPN.isDisable) {
        if (!IPN.value) {
            errors.IPN = '    '
        } else {
            if (IPN.value.length !== 10) {
                errors.IPN = '   10 '
            } else {
                const findNotNumber = [...IPN.value].find(el => isNaN(+el))
                if (findNotNumber) errors.IPN = '     '
            }
        }
    }

    if (!fatherName.isDisable && !fatherName.value) errors.fatherName = '    '

    if (!passportNum) {
        errors.passportNum = '    '
    } else {
        let count = 0;
        [...passportNum].forEach(el => !isNaN(+el) && count++)
        if (count !== 9) errors.passportNum = '  ,    9 '
    }

    if (uniqueNumber) {
        if (uniqueNumber.length === 14) {
            const stringConcat = [...uniqueNumber].slice(0, 8).concat([...uniqueNumber].slice(9, 14))
            const findNotNumber = stringConcat.find(el => isNaN(+el))
            if (findNotNumber) errors.uniqueNumber = '     '
            if (uniqueNumber[8] !== '-') errors.uniqueNumber = '    -'
        } else {
            if (event.uniqueNumber.length === 13) {
                if (![...uniqueNumber].includes('-')) {
                    errors.uniqueNumber = '    -'
                } else {
                    errors.uniqueNumber = '  ,    13 '
                }
            } else {
                errors.uniqueNumber = '  ,    13 '
            }
        }
    }

    return errors
}